<template>
  <div class="menu" @click="togglemenu">
    <div v-if="showbars" class="bars">
      <div class="bar" @click="removebook(bookid)">删除</div>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    bookid: String,
    removebook: Function,
  },
  data () {
    return {
      showbars: false
    }
  },
  methods: {
    togglemenu () {
      this.showbars = !this.showbars;
    }
  }
}
</script>
<style scoped>
.menu {
  position: absolute;
  right: 10px;
  top: 10px;
  width: 20px;
  height: 20px;
  background-image: url("../../imgs/menu.png");
  background-repeat: no-repeat;
  background-size: 20px 20px;
}
.bars {
  position: absolute;
  top: 29px;
  right: 0;
  width: 100px;
  text-align: center;
  border: 1px solid #ccc;
  background-color: #fff;
}
.bar {
  width: 80px;
  margin: 10px;
  height: 24px;
  line-height: 24px;
  background-color: cornsilk;
}
</style>