import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    mybooks: [],
    curent: null,
    setting: {
      layout_tips_style: {
        backgroundColor: '#fff',
      },
      chapter_tips_style: {
        fontSize: '16px',
        color: '#333'
      },
      title_tips_style: {
        color: '#333'
      }
    }
  },
  mutations: {
    ADD_BOOK (state, book) {
      // console.log('添加成功');
      state.mybooks.push(book);
      localStorage.setItem('mybooks', JSON.stringify(state.mybooks));
    },
    REMOVE_BOOK (state, bookid) {
      console.log(bookid)
      // console.log('删除成功');
      state.mybooks = state.mybooks.filter(book => book._id !== bookid);
      localStorage.setItem('mybooks', JSON.stringify(state.mybooks));
    },
    INIT_MYBOOKS (state, books) {
      state.mybooks = books;
    },
    UPDATE_MYBOOKS (state, books) {
      state.mybooks = books;
      localStorage.setItem('mybooks', JSON.stringify(state.mybooks));
    },
    INIT_SETTING (state, setting) {
      state.setting = setting;
    },
    UPDATE_SETTING (state, setting) {
      state.setting = setting;
      localStorage.setItem('setting', JSON.stringify(state.setting));
    }
  },
  actions: {

  },
  modules: {
  }
})
